<template>
  <div class="container">
    <el-card class="box-card">
      <!-- 头部 -->
      <div class="btn-top">
        <span data-v-48e452ac="" style="font-size: 12px; color: pink"
          >说明：目前支持学科和关键字条件筛选</span
        >
        <el-button
          class="el-btn"
          type="success"
          size="small"
          @click="$router.push('new')"
          ><i class="el-icon-edit" style="margin-right: 5px"></i
          >新增试题</el-button
        >
      </div>
      <!-- form区域 -->
      <el-form type="flex" ref="form" label-width="80px">
        <el-row>
          <el-form-item label="学科" class="el-col el-col-6">
            <el-select v-model="pages.subjectID" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="二级目录" class="el-col el-col-6">
            <el-select v-model="form.second" placeholder="请选择">
              <el-option :value="form.second"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="标签" class="el-col el-col-6">
            <el-select v-model="form.tags" placeholder="请选择">
              <el-option :value="form.tags"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="关键字" class="el-col el-col-6">
            <el-input
              v-model="pages.keyword"
              placeholder="根据题干搜索"
            ></el-input>
          </el-form-item>
          <el-form-item label="试题类型" class="el-col el-col-6">
            <el-select v-model="form.questionType" placeholder="请选择">
              <el-option
                v-for="item in topic"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="难度" class="el-col el-col-6">
            <el-select v-model="form.difficulty" placeholder="请选择">
              <el-option
                v-for="item in difficultyList"
                :key="item.id"
                :value="item.label"
                :label="item.label"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="方向" class="el-col el-col-6">
            <el-select v-model="form.direction" placeholder="请选择">
              <el-option
                v-for="item in direction"
                :key="item.value"
                :value="item.label"
                :label="item.label"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="录入人" class="el-col el-col-6">
            <el-select v-model="form.creator" placeholder="请选择">
              <el-option
                v-for="item in man"
                :key="item.value"
                :value="item.value"
                :label="item.label"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="题目备注"
            class="el-col el-col-6"
            label-width="80px"
          >
            <el-input></el-input>
          </el-form-item>
          <el-form-item
            label="企业简称"
            class="el-col el-col-6"
            label-width="80px"
          >
            <el-input></el-input>
          </el-form-item>
          <el-form-item label="城市" class="el-col el-col-6" label-width="80px">
            <el-select
              @change="change"
              v-model="form.provinces"
              placeholder="请选择"
              style="width: 49%"
            >
              <el-option
                v-for="item in provinces"
                :key="item"
                :value="item"
                :label="item"
              >
              </el-option>
            </el-select>
            <el-select
              v-model="form.citys"
              placeholder="请选择"
              style="width: 49%; margin-left: 2%"
            >
              <el-option
                v-for="item in citys"
                :key="item"
                :value="item"
                :label="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="text-align: right" class="el-col ct el-col-6">
            <el-button size="small" @click="clear">清除</el-button>
            <el-button size="small" type="primary" @click="search"
              >搜索</el-button
            >
          </el-form-item>
        </el-row>
      </el-form>
      <!-- 数据条数 -->
      <el-alert
        :title="'数据一共' + total + '条'"
        type="info"
        show-icon
         :closable="false"
      ></el-alert>
      <!-- 表格展示 -->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="number" label="试题编号" width="230">
        </el-table-column>
        <el-table-column prop="subject" label="学科" width="100">
        </el-table-column>
        <el-table-column prop="catalog" label="目录"> </el-table-column>
        <el-table-column label="题型">
          <template #default="{ row }">
            {{
              row.questionType === '1'
                ? '单选'
                : row.questionType === '2'
                ? '多选'
                : row.questionType === '3'
                ? '简答'
                : ''
            }}
          </template>
        </el-table-column>
        <el-table-column width="280px" prop="question" label="题干">
          <template #default="{ row }">
            {{ replaceHtmlTag(row.question) }}
          </template>
        </el-table-column>
        <el-table-column width="200px" label="录入时间">
          <template #default="{ row }">
            {{ row.addDate | parseTimeByString }}
          </template>
        </el-table-column>
        <el-table-column prop="difficulty" label="难度">
          <template #default="{ row }">
            {{
              row.difficulty === '1'
                ? '简单'
                : row.difficulty === '2'
                ? '一般'
                : row.difficulty === '3'
                ? '困难'
                : ''
            }}
          </template>
        </el-table-column>
        <el-table-column prop="creator" label="录入人"> </el-table-column>
        <el-table-column width="200px" label="操作">
          <template #default="{ row }">
            <el-row>
              <el-button
                plain
                type="primary"
                @click="look(row)"
                icon="el-icon-view"
                circle
              ></el-button>
              <el-button
                plain
                @click="goNewEdit(row)"
                type="success"
                icon="el-icon-edit"
                circle
              ></el-button>
              <el-button
                plain
                type="danger"
                @click="delItem(row)"
                icon="el-icon-delete"
                circle
              ></el-button>
              <el-button
                plain
                type="warning"
                @click="choiceAdd(row)"
                icon="el-icon-check"
                circle
              ></el-button>
            </el-row>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <!--       @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage4" -->
      <el-pagination
        style="float: right; margin: 20px"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="pages.pagesize"
        layout="prev, pager , next ,sizes , jumper"
        :total="total"
        hide-on-single-page
      >
      </el-pagination>
      <!-- dialog弹层 -->

      <Container ref="questionPreview" :data="dialogData"></Container>
    </el-card>
  </div>
</template>

<script>
// choiceAdd
import { list, remove, choiceAdd } from '@/api/hmmm/questions'
import { simple } from '@/api/hmmm/subjects'
import { provinces, citys } from '@/api/hmmm/citys'
export default {
  data () {
    return {
      total: 0,
      isShow: false,
      checked: 1,
      checkboxList: false,
      pages: {
        page: 1,
        pagesize: 10,
        subjectID: null,
        keyword: null
      },
      tableData: [], // 表格数据
      form: {
        value: '', // 学科下拉列表
        second: '', // 二级目录
        tags: '', // 标签
        keyword: '', // 关键词搜索
        questionType: '', // 题目类型
        difficulty: '', // 难度下拉列表的value
        direction: '', // 方向下拉列表
        creator: '', // 录入人
        provinces: '', // 省
        citys: '' // 城市
      },
      options: [],
      // 假数据
      topic: [
        { id: 10090, label: '单选' },
        { id: 10091, label: '多选' },
        { id: 10092, label: '简答' }
      ],
      difficultyList: [
        { id: 1, label: '简单' },
        { id: 2, label: '一般' },
        { id: 3, label: '困难' }
      ],
      direction: [
        { value: 1, label: 'o2o' },
        { value: 2, label: '外包服务' },
        { value: 3, label: '企业服务' },
        { value: 4, label: '互联网金融' },
        { value: 5, label: '企业咨询' },
        { value: 6, label: '互联网' },
        { value: 7, label: '电子商务' },
        { value: 8, label: '其他' }
      ],
      man: [
        { value: 1, label: '超级管理员' },
        { value: 2, label: '录入管理员' },
        { value: 3, label: 'demo' }
      ],
      provinces: [], // 省
      citys: [],
      dialogVisible: false, // 弹层
      dialogData: {} // 弹层需要的数据
    }
  },
  watch: {
    dialogVisible (newVal) {
      // console.log('---------------------', newVal)
      this.isShow = !newVal
    }
  },
  async created () {
    this.subjectID = this.form.value
    this.getDataList()
    const res = await simple()
    // console.log(res)
    this.options = res.data
    // console.log(this.options)
    const result = await provinces()
    // console.log(result)
    this.provinces = result
  },
  methods: {
    async clear () {
      // console.log(123)
      this.form.provinces = null
      this.form.citys = null
      this.form.questionType = null
      this.form.difficulty = null
      this.form.direction = null
      this.form.creator = null
      this.pages.subjectID = null
      this.pages.keyword = null
      this.pages.page = 1
      this.getDataList()
    },
    handleSizeChange (val) {
      // console.log(val)
      this.pages.pagesize = val
      this.pages.page = 1
      this.getDataList()
    },
    handleCurrentChange (val) {
      // console.log(val)
      this.pages.page = val
      this.getDataList()
    },
    openVideo () {
      this.isShow = true
    },
    async look (row) {
      this.dialogData = row
      // console.log(this.dialogData)
      this.$nextTick(() => {
        this.$refs.questionPreview.open()
      })
    },
    async change () {
      this.form.citys = ''
      const city = await citys(this.form.provinces)
      // console.log(city)
      this.citys = city
    },
    async getDataList () {
      // console.log(this.pages)
      // console.log(this.pages.keyword)
      // this.pages.keyword

      const res = await list(this.pages)
      // console.log('13123123123', res)
      this.tableData = res.data.items
      this.total = res.data.counts
      // console.log('列表数据', this.tableData)
    },
    replaceHtmlTag (str) {
      // console.log(str)
      return str.replace(/<[^>]+>/g, '').replace(/&nbsp;/gi, '')
    },
    goNewEdit ({ id }) {
      console.log(id)
      // $router.push(`/questions-new/${row.id}`)
      this.$router.push({
        path: '/questions/new',
        query: {
          id
        }
      })
    },
    delItem (row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await remove(row)
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getDataList()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    choiceAdd ({ id }) {
      console.log(id)
      this.$confirm('此操作将该题目加入精选, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const res = await choiceAdd({ id: id, choiceState: 1 })
          this.getDataList()
          console.log(res)
          this.$message({
            type: 'success',
            message: '添加成功!!!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消!!!'
          })
        })
    },
    async search () {
      // console.log(this.pages.keyword)
      this.subjectID = this.form.value
      // console.log(this.form.value)
      this.getDataList()
    }
  }
}
</script>

<style scoped lang="scss">
.container {
  padding: 10px;
}

.dialog {
  // height: 1000px;
  .el-dialog__wrapper {
    height: 100%;
  }
}
.btn-top {
  height: 32px;
  margin-bottom: 15px;
}
.el-form-item__content {
  margin-left: 100px;
}
.el-btn {
  padding: 9px 15px;
  float: right;
}
.el-select {
  width: 100%;
}
.el-col {
  float: left;
}
.el-col-6 {
  width: 25%;
}
.subjType {
  margin: 15px 0;
}
.el-btn {
  padding: 9px 15px;
  float: right;
}

.el-col {
  float: left;
}
.el-col-6 {
  width: 25%;
}
.subjType {
  margin: 15px 0;
}
